import React from 'react';

class Navigation extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            links: []
        }

        for(var i=0; i<props.pages.length; i++) {
            addNavbarEntry(props.pages[i].linkText, 
                props.pages[i].activeLink, 
                props.pages[i].linkTo);
        }
    }

    addNavbarEntry(linkText, activeLink, linkTo) {
        if(activeLink) {
            this.setState(prevState => ({
                links: [...prevState.links, (
                    <li class="active"><a href="{linkTo}">{linkText}</a></li>
                )]
            }))
        } else {
            this.setState(prevState => ({
                links: [...prevState.links, (
                    <li><a href="{linkTo}">{linkText}</a></li>
                )]
            }))
        }
    }

    render() {
        return (
            <nav>
                <div class="nav-wrapper">
                    <a href="{this.props.pages.home.linkTo}" class="brand-logo center">UMEET</a>
                    <ul class="left hide-on-med-and-down">
                        {this.state.links}
                    </ul>
                </div>
            </nav>
        )
    }
}

export default Navigation;